.mobile-nav {
  padding-top: 40px;
  background: $bg-white;
  display: block;
  height: 100%;
  left: 0;
  overflow: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  opacity: 0;
  -webkit-transform: translateX(200%);
  transform: translateX(200%);
  transition: all ease 0.5s;

  &--active {
    opacity: 1;
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }

  &__close-btn {
    width: 40px;
    height: 40px;
    background-color: transparent;
    background-image: url("../img/close.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    color: #1a237e;
    cursor: pointer;
    font-size: 28px;
    opacity: 0.6;
    outline: none;
    position: absolute;
    right: 10px;
    top: 10px;
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
    z-index: 2;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {

  }

  &__link {
    display: block;
    padding: 20px 0 20px;
    color: $link-light-blue;
    text-decoration: underline;
    text-align: center;
    font-size: 22px;

    &:hover{
      background-color: $hover-light-grey;
    }

    &--active {
      background-color: $main-blue;
      color: #ffffff;
      text-decoration: none;

      &:hover {
        background-color: $main-blue-dark;
      }
    }
  }
}
