.footer-bottom {
  background-color: #e1e3ed;
  min-height: 60px;

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-top: 5px;
    color: #7293b4;
    font-size: 12px;
    line-height: 30px;
  }

  &__copyrights {
    flex-grow: 1;
  }

  &__links {
    width: 462px;
    padding-left: 46px;

    a {
      text-decoration: underline;

      &:first-of-type {
        margin-right: 36px;
      }
    }
  }

  &__developed {
    position: absolute;
    right: 0;
    bottom: 2px;
    width: 46px;
    height: 50px;
    background-color: #000000;
  }
}

@media (max-width: $tablet-width) {

  .footer-bottom {

    &__content {
      flex-direction: column-reverse;
    }

    &__copyrights {
      max-width: 100%;
    }

    &__links {
      width: 100%;
      padding-left: 0;

      &>a {
        display: block;
      }
    }
  }
}
