.about {
  padding: 90px 0 90px;

  .about-content {
    display: flex;
    justify-content: space-between;

    &__column {
      width: 49%;
    }

    &__title {
      font-weight: bold;
      font-size: 24px;
      line-height: 24px;
      color: $main-blue;
      text-transform: uppercase;
    }

    &__subtitle {
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;
      color: $main-blue;
      text-transform: uppercase;
    }

    &__list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &__image {
      max-width: 690px;
    }
  }
}


@media (max-width: $tablet-width) {
  .about {
    padding: 70px 0 70px;

    .about-content {
      flex-direction: column;

      &__column {
        width: 100%;
      }

      &__image {
        max-width: 100%;
      }
    }
  }

}
