.footer-main {
  border-top: 1px solid #d3d7eb;
  background-color: #f6f7fa;

  .footer-main-content {
    position: relative;
    display: flex;
    justify-content: space-between;
  }

  .footer-nav {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    padding-top: 48px;
    padding-right: 46px;
    padding-bottom: 42px;

    &__column-title {
      margin-top: 0;
      font-size: 22px;
      color: $main-blue;
      margin-bottom: 30px;
    }

    &__list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &__item {

    }

    &__link {
      font-size: 18px;
      line-height: 30px;
      color: #7293b4;

      &:hover {
        color: $main-blue;
        text-decoration: underline;
      }
    }
  }

  .footer-contacts {
    width: 462px;
    padding-top: 48px;
    padding-left: 46px;
    padding-bottom: 42px;
    background-image: url("../img/white-shadow.png");
    background-repeat: no-repeat;
    background-position: center left;
    border-left: 1px solid #d3d7eb;

    & p {
      margin-top: 10px;
      margin-bottom: 10px;
    }

    &__primary {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 42px;
      margin: 0;
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 16px;
      line-height: 36px;
      color: $main-blue;

      &::before {
        position: absolute;
        content: "";
        top: 5px;
        left: 0;
        width: 28px;
        height: 25px;
        background-image: url("../img/phone (1).svg");
        background-repeat: no-repeat;
        opacity: 0.5;
      }
    }

    &__phone1 {
      margin-right: 14px;
    }

    &__viber {
      width: 20px;
      height: 20px;
      margin-right: 4px;
      background-image: url("../img/viber.svg");
      background-repeat: no-repeat;
    }

    &__telegram {
      width: 20px;
      height: 20px;
      background-image: url("../img/telegram.svg");
      background-repeat: no-repeat;
    }

    &__phone2 a{
      position: relative;
      margin-right: 32px;
      padding-left: 40px;
      color: $main-blue;
      font-size: 16px;
      line-height: 36px;

      &::before {
        position: absolute;
        content: "";
        top: -4px;
        left: 0;
        width: 26px;
        height: 26px;
        background-image: url("../img/phone (2).svg");
        background-repeat: no-repeat;
        opacity: 0.5;
      }
    }

    &__email {
      margin-bottom: 26px;

      a {
        position: relative;
        padding-left: 42px;
        color: $main-blue;
        text-decoration: underline;
        font-size: 16px;
        line-height: 36px;

        &::before {
          position: absolute;
          content: "";
          top: -2px;
          left: 0;
          width: 26px;
          height: 26px;
          background-image: url("../img/mail (1).svg");
          background-repeat: no-repeat;
          opacity: 0.5;
        }
      }
    }

    &__address {
      margin-bottom: 30px;

      .address-primary {
        position: relative;
        padding-left: 44px;
        margin: 0;
        margin-bottom: 8px;
        font-weight: bold;
        font-size: 30px;
        line-height: 36px;
        color: $green-accent;

        &::before {
          position: absolute;
          content: "";
          top: 5px;
          left: 0;
          width: 28px;
          height: 25px;
          background-image: url("../img/moscow.svg");
          background-repeat: no-repeat;
        }
      }

      .address-secondary {
        position: relative;
        padding-left: 44px;
        margin: 0;
        font-size: 16px;
        line-height: 36px;

        &::before {
          position: absolute;
          content: "";
          top: 4px;
          left: 0;
          width: 26px;
          height: 26px;
          background-image: url("../img/marker.svg");
          background-repeat: no-repeat;
        }
      }
    }

  }

  .footer-up-btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 89px;
    height: 89px;
    background-color: $green-accent;
    background-image: url("../img/up-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;

    &:hover {
      background-color: #598a05;
    }
  }

  .footer-up-btn:active, .footer-up-btn:focus {
    outline: none;
    opacity: 0.7;
  }
  .footer-up-btn::-moz-focus-inner {
    border: 0;
  }
}



@media (max-width: $laptop-width) {

  .footer-main {

    .footer-main-content {
      flex-direction: column-reverse;
    }

    .footer-nav {
      padding-top: 20px;
      flex-wrap: wrap;

      &__column {
        margin-bottom: 20px;
      }

    }

    .footer-contacts {
      padding-top: 0;
      padding-left: 0;
      border-left: none;
      border-bottom: 1px solid #d3d7eb;
      width: 100%;
      padding-bottom: 20px;
    }
  }

}

@media (max-width: $tablet-width) {

  .footer-main {

    .footer-main-content {
      flex-direction: column-reverse;
    }

    .footer-nav {
      padding-top: 20px;
      flex-wrap: wrap;

      &__column {
        margin-bottom: 20px;
      }
    }

    .footer-contacts {
      padding-top: 0;
      padding-left: 0;
      border-left: none;
      border-bottom: 1px solid #d3d7eb;
      width: 100%;
      padding-bottom: 20px;

      &>img {
        width: 120px;
      }

      &__address {

        .address-primary {
          font-size: 22px;
          line-height: 28px;
        }
      }
    }

    .footer-up-btn {
      width: 60px;
      height: 60px;
    }
  }

}
