.top-nav {
  &__list {
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
    min-height: 42px;
  }

  &__item {
    flex-grow: 1;
    border-left: 1px solid $hover-light-grey;

    &:last-of-type {
      border-right: 1px solid $hover-light-grey;
    }
  }

  &__link {
    display: block;
    min-height: 42px;
    padding-top: 2px;
    font-size: 16px;
    line-height: 36px;
    color: $link-light-blue;
    text-decoration: underline;
    text-align: center;

    &:hover{
      background-color: $hover-light-grey;
    }

    &--active {
      background-color: $main-blue;
      color: #ffffff;
      text-decoration: none;

      &:hover {
        background-color: $main-blue-dark;
      }
    }
  }
}

@media (max-width: $laptop-width) {
  .top-nav {
    &__link {
      font-size: 14px;
    }
  }
}

@media (max-width: $tablet-width) {
  .top-nav {
    display: none;
  }
}
