.section-heading {
  padding-top: 18px;
  padding-left: 22px;
  border-left: 8px solid $green-accent;
  min-height: 100px;
  margin-bottom: 50px;

  .section-title {
    margin: 0 0 28px;
    font-weight: bold;
    font-size: 48px;
    line-height: 60px;
  }

  .section-lead {
    font-size: 18px;
    line-height: 24px;
  }
}

@media (max-width: $tablet-width) {
  .section-heading {
    .section-title {
      margin: 0 0 22px;
      font-weight: bold;
      font-size: 40px;
      line-height: 52px;
    }

    .section-lead {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

@media (max-width: $mobile-width) {
  .section-heading {
    padding-top: 12px;
    padding-left: 18px;

    .section-title {
      margin: 0 0 18px;
      font-weight: bold;
      font-size: 28px;
      line-height: 36px;
    }

    .section-lead {
      font-size: 16px;
      line-height: 22px;
    }
  }
}
