.advantages {
  padding: 60px 0 100px;
  border-bottom: 1px solid $hover-light-grey;

  .advantages-list {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .advantages-item {
    width: 235px;
    box-sizing: border-box;

    &__img-wrap {
      width: 100%;
      height: 90px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &__icon {

    }

    &__name {
      text-align: center;
      font-weight: bold;
      font-size: 22px;
      color: $main-blue;
    }
  }
}

@media (max-width: $tablet-width) {

  .advantages {
    padding: 50px 0 80px;

    .advantages-item {
      width: 220px;
      box-sizing: border-box;

      &__name {
        font-size: 18px;
      }
    }
  }
}
