.clients {
  position: relative;
  padding: 120px 0 120px;
  background-color: $bg-white;

  .clients-slider {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;

    &__wrapper {
      display: flex;
      transition: transform 0.6s ease;
      width: 100%;
    }

    &__slide {
      flex: 0 0 25%;
      max-width: 25%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &__controls {
      position: absolute;
      z-index: 2;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 80px;

      .cslider-btn {
        width: 42px;
        height: 42px;
        background-color: $bg-white;
        font-size: 0;
        border: none;
        background-image: url("../img/slide-arrow.svg");
        background-position: center;
        background-repeat: no-repeat;
        box-shadow: 0px 0px 4px 0px rgba(37, 37, 37, 0.4);
        opacity: 0.2;
        transition: all 0.2s;

        &:hover {
          opacity: 1;
        }

        &--left {
          transform: rotate(180deg);
        }
      }
    }
  }
}

@media (max-width: $laptop-width) {
  .clients {
    padding: 80px 0 80px;

    .clients-slider {
      &__slide {
        flex: 0 0 50%;
        max-width: 50%;
      }

      &__controls {
        .cslider-btn {
          width: 32px;
          height: 40px;
        }
      }
    }
  }
}
