.indicators {
  padding: 70px 0 75px;
  border-bottom: 1px solid $hover-light-grey;

  &-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 330px;
    height: 209px;
    padding: 20px;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
    background-repeat: no-repeat;
    background-size: contain;

    &__lead {
      margin: 0 0 30px;
      font-weight: bold;
      font-size: 72px;

    }

    &__descr {
      margin: 0;
      font-weight: bold;
      font-size: 22px;
      color: $main-blue;
      text-align: center;
    }

    &.col-green {
      background-image: url("../img/indicators-bg-1.png");

      .indicators-item__lead {
        color: #70a513;
      }
    }

    &.col-yellow {
      background-image: url("../img/indicators-bg-2.png");

      .indicators-item__lead {
        color: #f0c808;
      }
    }

    &.col-blue {
      background-image: url("../img/indicators-bg-3.png");

      .indicators-item__lead {
        color: #06aed5;
      }
    }

    &.col-red {
      background-image: url("../img/indicators-bg-4.png");

      .indicators-item__lead {
        color: #fe5f55;
      }
    }
  }
}

@media (max-width: $tablet-width) {

  .indicators {

    &-item {
      padding: 20px;
      
      &__lead {
        margin: 0 0 20px;
        font-size: 52px;
      }

      &__descr {
        font-size: 18px;
      }

    }
  }
}
