.technologies {
  padding: 120px 0 90px;
  background-image: url("../img/shadow.png");
  background-repeat: no-repeat;
  background-position: top center;

  .technologies-header {
    display: flex;
    justify-content: space-between;

    .link-more {
      align-self: start;
      padding: 20px 40px;
      font-size: 22px;
      font-weight: bold;
      background-color: $green-accent;
      color: $bg-white;
    }
  }

  .technologies-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    .technologies-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin: 0;
      margin-right: 30px;
      padding: 0;
      list-style: none;
    }

    .technologies-item {
      display: flex;
      flex-direction: column;
      width: 210px;
      height: 240px;
      margin-bottom: 32px;

      &__image {
        width: 210px;
        height: 180px;
        background-color: $hover-light-grey;
      }

      &__title {
        margin-top: 12px;
        padding-left: 10px;
        padding-right: 10px;
        font-weight: bold;
        text-align: center;
        font-size: 22px;

        a {
          color: $main-blue;
        }
      }
    }

    .technologies-widget {
      width: 450px;
      min-height: 500px;
      padding: 30px 28px 14px;
      box-shadow: 0px 0px 8px 0px rgba(37, 37, 37, 0.4);

      &__title {
        color: $main-blue;
      }
    }
  }
}


@media (max-width: $laptop-width) {

  .technologies {
    .technologies-header {
      flex-direction: column;
      margin-bottom: 30px;
    }

    .technologies-content {
      flex-direction: column;

      .technologies-item {
        margin-left: auto;
        margin-right: auto;

        &__title {
          font-size: 18px;
        }
      }

      .technologies-widget {
        margin-left: auto;
        margin-right: auto;
        max-width: 100%;

        &__video {
          width: 100%;
        }
      }
    }
  }
}
