.overlay {
  background: rgba(0,0,0,0.3);
  display: block;
  height: 100%;
  left: 0;
  overflow: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 20;
  opacity: 0;
  -webkit-transform: translateY(-4000px);
  transform: translateY(-4000px);

  &--active {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}
