.main-slider {
  position: relative;
  overflow: hidden;
  height: 500px;
  background-color: $hover-light-grey;

  &__controls {
    position: absolute;
    z-index: 5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 500px;
    padding: 0 37px;

    .slider-btn {
      width: 80px;
      height: 80px;
      background-color: $bg-white;
      font-size: 0;
      border: none;
      background-image: url("../img/slide-arrow.svg");
      background-position: center;
      background-repeat: no-repeat;
      box-shadow: 0px 0px 8px 0px rgba(37, 37, 37, 0.4);

      &--left {
        transform: rotate(180deg);
      }
    }
  }

  &__wrapper {
    display: flex;
    transition: transform 0.6s ease;
  }

  &__slide {
    flex: 0 0 100%;
    max-width: 100%;
    height: 500px;
    background-image: url("../img/slider1-bg.jpg");
    background-repeat: no-repeat;
    background-position: center;

    .slide-content {
      display: flex;
      align-items: center;
      height: 500px;
    }

    .text-string {
      font-weight: bold;

      .red-col {
        color: $red-accent;
      }

      &--first {
        margin: 0;
        font-size: 48px;
        line-height: 36px;
        color: $main-blue;
      }

      &--second {
        margin: 44px 0 60px;
        font-size: 100px;
        line-height: 36px;
        color: $main-blue;
      }

      &--third {
        padding: 18px 40px;
        font-size: 36px;
        color: $bg-white;
        background-color: $red-accent;
      }
    }
  }
}

@media (max-width: $laptop-width) {
  .main-slider {
    height: 400px;

    &__controls {
      padding: 0 10px;
      height: 400px;

      .slider-btn {
        width: 60px;
        height: 80px;
        opacity: 0.2;
        transition: all 0.2s;

        &:hover {
          opacity: 1;
        }
      }
    }

    &__slide {
      background-position: center;
      height: 400px;

      .slide-content {
        height: 400px;
      }

      .text-string {
        &--first {
          font-size: 36px;
        }

        &--second {
          margin: 36px 0 40px;
          font-size: 70px;
        }

        &--third {
          padding: 14px 30px;
          font-size: 26px;
        }
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .main-slider {
    height: 300px;

    &__controls {
      padding: 0 10px;
      height: 300px;

      .slider-btn {
        width: 60px;
        height: 80px;
        opacity: 0.2;
        transition: all 0.2s;

        &:hover {
          opacity: 1;
        }
      }
    }

    &__slide {
      height: 300px;

      .slide-content {
        justify-content: center;
        height: 300px;
      }

      .text-string {
        &--first {
          font-size: 28px;
        }

        &--second {
          margin: 28px 0 30px;
          font-size: 50px;
        }

        &--third {
          padding: 14px 30px;
          font-size: 22px;
        }
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .main-slider {
    height: 300px;

    &__controls {
      padding: 0 10px;
      height: 300px;

      .slider-btn {
        width: 50px;
        height: 60px;
      }
    }

    &__slide {

      .text-string {
        &--first {
          font-size: 22px;
        }

        &--second {
          margin: 10px 0 20px;
          font-size: 42px;
        }

        &--third {
          padding: 10px 20px;
          font-size: 18px;
        }
      }
    }
  }
}

