.main-head {
  position: relative;
  z-index: 2;
  box-shadow: 0px -5px 10px 0px rgba(0,0,0,0.1);

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 36px 0 36px;
  }

  &__langbox {
    display: flex;
    flex-direction: column;

    .langchange {
      width: 25px;
      height: 25px;
      background-color: blue;
      border-radius: 50%;
      border: 4px solid $hover-light-grey;
      background-repeat: no-repeat;
      background-position: center;

      &--ukr {
        margin-bottom: 15px;
        background-image: url("../img/ukr-flag.png");
      }

      &--rus {
        background-image: url("../img/rus-flag.png");
      }

      &--active {
        opacity: 0.3;
      }

    }
  }

  &__address {
    .address-primary {
      position: relative;
      padding-left: 44px;
      margin: 0;
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 30px;
      line-height: 36px;
      color: $main-blue;

      &::before {
        position: absolute;
        content: "";
        top: 5px;
        left: 0;
        width: 28px;
        height: 25px;
        background-image: url("../img/moscow.svg");
        background-repeat: no-repeat;
      }
    }

    .address-secondary {
      position: relative;
      padding-left: 44px;
      margin: 0;
      font-size: 16px;
      line-height: 36px;

      &::before {
        position: absolute;
        content: "";
        top: 4px;
        left: 0;
        width: 26px;
        height: 26px;
        background-image: url("../img/marker.svg");
        background-repeat: no-repeat;
      }
    }
  }

  &__contacts {
    .contacts-primary {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 42px;
      margin: 0;
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 30px;
      line-height: 36px;
      color: $main-blue;

      &::before {
        position: absolute;
        content: "";
        top: 5px;
        left: 0;
        width: 28px;
        height: 25px;
        background-image: url("../img/phone (1).svg");
        background-repeat: no-repeat;
      }

      &__phone1 {
        margin-right: 14px;
      }

      &__viber {
        width: 31px;
        height: 31px;
        margin-right: 4px;
        background-image: url("../img/viber.svg");
        background-repeat: no-repeat;
      }

      &__telegram {
        width: 31px;
        height: 31px;
        background-image: url("../img/telegram.svg");
        background-repeat: no-repeat;
      }
    }

    .contacts-secondary {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 36px;

      &__phone2 {
        position: relative;
        margin-right: 32px;
        padding-left: 40px;
        color: #000000;

        &::before {
          position: absolute;
          content: "";
          top: 4px;
          left: 0;
          width: 26px;
          height: 26px;
          background-image: url("../img/phone (2).svg");
          background-repeat: no-repeat;
        }
      }

      &__email {
        position: relative;
        padding-left: 42px;
        color: $main-blue;
        text-decoration: underline;

        &::before {
          position: absolute;
          content: "";
          top: 4px;
          left: 0;
          width: 26px;
          height: 26px;
          background-image: url("../img/mail (1).svg");
          background-repeat: no-repeat;
        }
      }


    }
  }

  &__menu-btn {
    position: relative;
    z-index: 25;
    width: 60px;
    height: 60px;
    background-color: $bg-white;
    border: none;
    display: none;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      width: 40px;
      height: 4px;
      margin-top: -2px;
      margin-left: -20px;
      background-color: $main-blue-dark;
      box-shadow: 0px 10px 0px 0 $main-blue-dark,
                  0px -10px 0px 0 $main-blue-dark;
    }

    &:hover {
      background-color: $hover-light-grey;
    }
  }

  .main-head__menu-btn:active, .main-head__menu-btn:focus {
    outline: none;
    opacity: 0.7;
  }
  .main-head__menu-btn::-moz-focus-inner {
    border: 0;
  }
}

@media (max-width: $desktop-width) {
  .main-head {
    &__content {
      padding: 18px 0 18px;
    }

    &__langbox {
      padding-left: 10px;
      padding-right: 10px;
    }

    &__address {
      padding-left: 10px;
      padding-right: 10px;

      .address-primary {
        padding-left: 30px;
        font-size: 18px;
        line-height: 36px;
      }

      .address-secondary {
        padding-left: 30px;
        font-size: 14px;
        line-height: 28px;

        &::before {
          width: 26px;
          height: 26px;
        }
      }
    }

    &__contacts {
      padding-left: 10px;
      padding-right: 10px;

      .contacts-primary {
        flex-wrap: wrap;
        padding-left: 30px;
        font-size: 16px;
        line-height: 28px;

        &__viber {
          width: 24px;
          height: 24px;
        }

        &__telegram {
          width: 24px;
          height: 24px;
        }
      }

      .contacts-secondary {
        flex-direction: column;
        align-items: flex-start;
        font-size: 16px;
        line-height: 28px;

        &__phone2 {
          margin-right: 0;
          padding-left: 30px;

          &::before {
            width: 20px;
            height: 20px;
          }
        }

        &__email {
          padding-left: 30px;

          &::before {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }
}


@media (max-width: $tablet-width) {
  .main-head {
    &__content {
      padding: 18px 0 18px;
    }

    &__logo {
      width: 120px;
    }

    &__langbox {
      flex-direction: row;

      .langchange--ukr {
        margin-right: 20px;
      }
    }

    &__address {
      display: none;
    }

    &__contacts {
      display: none;
    }

    &__menu-btn {
      display: block;
    }
  }
}
