.services {
  padding: 120px 0 90px;
  background-image: url("../img/shadow.png");
  background-repeat: no-repeat;
  background-position: top center;
  box-shadow: 0px 2px 13px 3px rgba(41,41,41,0.3);

  .services-header {
    display: flex;
    justify-content: space-between;

    .link-more {
      align-self: start;
      padding: 20px 40px;
      font-size: 22px;
      font-weight: bold;
      background-color: $red-accent;
      color: $bg-white;
    }
  }

  .services-gallery {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .service-item {
    width: 330px;
    height: 350px;
    margin-bottom: 28px;
    background-color: $bg-white;

    &__picture {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 330px;
      height: 240px;
      background-image: url("../img/service-item-bg.jpg");
      background-repeat: no-repeat;
      background-size: contain;

      img {
        opacity: 0.5;
      }

    }

    &__title {
      margin-top: 20px;
      margin-bottom: 14px;
      font-weight: bold;
      font-size: 24px;
      color: $main-blue;
      text-align: center;
    }

    &__descr {
      font-size: 18px;
      line-height: 14px;
      text-align: center;
    }

    &:hover .service-item__picture {
      background-image: url("../img/service-item-bg-active.jpg");

      img {
        opacity: 1;
      }
    }
  }
}


@media (max-width: $laptop-width) {

  .services {
    .services-header {
      flex-direction: column;
      margin-bottom: 30px;
    }
  }
}

@media (max-width: $laptop-width) {

  .services {

    .service-item {
      width: 280px;
      height: 300px;
      margin-left: auto;
      margin-right: auto;

      &__picture {
        width: 280px;
        height: 204px;

        img {
          width: 90%;
        }
      }
    }
  }
}
