.shop-nav {
  background-color: $main-blue;

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
  }

  &__btn {
    position: relative;
    z-index: 25;
    width: 90px;
    height: 60px;
    margin-right: 30px;
    background-color: $main-blue-dark;
    border: none;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      width: 50px;
      height: 5px;
      margin-top: -2.5px;
      margin-left: -25px;
      background-color: $bg-white;
      box-shadow: 0px 12px 0px 0 #fff,
                  0px -12px 0px 0 #fff;
    }

    &:hover {
      background-color: $main-blue-light;
    }
  }

  .shop-nav__btn:active, .shop-nav__btn:focus {
    outline: none;
    opacity: 0.7;
  }
  .shop-nav__btn::-moz-focus-inner {
    border: 0;
  }

  .dropdown-categories {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 25;
    width: 100%;
    padding: 15px 0 5px 0;
    background-color: $bg-white;
    box-shadow: 0 4px 18px 0 rgba(48,63,159,0.1),
                0 2px 4px 0 rgba(92,107,192,0.18),
                0 0 20px 0 rgba(232,234,246,0.5);
    border-radius: 5px;
    display: none;

    &::before {
      position: absolute;
      content: "";
      top: -7px;
      left: 28px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 6px 8px 6px;
      border-color: transparent transparent $bg-white transparent;
      display: none;
    }

    &__close-btn {
      width: 40px;
      height: 40px;
      background-color: transparent;
      background-image: url("../img/close.svg");
      background-repeat: no-repeat;
      background-position: center;
      border: none;
      -webkit-box-shadow: none;
      box-shadow: none;
      color: #1a237e;
      cursor: pointer;
      font-size: 28px;
      opacity: 0.6;
      outline: none;
      position: absolute;
      right: 0;
      top: 0;
      -webkit-transition: opacity .3s;
      transition: opacity .3s;
      z-index: 2;
    }

    &__list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &__item {
      margin-bottom: 8px;
      padding: 0 15px;
    }

    &__link {

      &:hover {
        color: $main-blue;
        text-decoration: underline;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: stretch;
    justify-content: stretch;
    flex-grow: 1;
  }

  &__link {
    flex-grow: 1;
    font-size: 22px;
    line-height: 58px;
    font-weight: bold;
    color: #ffffff;
    text-align: center;

    &:hover {
      background-color: $main-blue-dark;
    }
  }

  .dropdown-categories.show {
    display: block;

    &::before {
      display: block;
    }
  }



  &__dropdown {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 10;
    width: 100%;
    padding: 15px 0 5px 0;
    background-color: $bg-white;
    box-shadow: 0 4px 18px 0 rgba(48,63,159,0.1),
                0 2px 4px 0 rgba(92,107,192,0.18),
                0 0 20px 0 rgba(232,234,246,0.5);
    border-radius: 5px;
    display: none;

    &::before {
      position: absolute;
      content: "";
      top: -7px;
      left: 28px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 6px 8px 6px;
      border-color: transparent transparent $bg-white transparent;
      display: none;
    }

    .dropdown-list {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    .dropdown-item {
      margin-bottom: 8px;
      padding: 0 15px;
    }

    .dropdown-link {

      &:hover {
        color: $main-blue;
        text-decoration: underline;
      }
    }
  }

  &__item:hover>.shop-nav__dropdown {
    display: block;

    &::before {
      display: block;
    }
  }
}

@media (max-width: $desktop-width) {
  .shop-nav {
    &__content {
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    &__list {
      width: 100%;
      justify-content: flex-start;
      flex-wrap: wrap;
    }

    &__item {
      position: relative;
      display: flex;
      align-items: stretch;
      justify-content: stretch;
      flex-grow: 1;
    }

    &__link {
      padding-left: 20px;
      padding-right: 20px;
      flex-grow: 1;
    }
  }
}

@media (max-width: $tablet-width) {
  .shop-nav {
    &__list {
      display: none;
    }
  }
}
